@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, "Source Sans Pro", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

*{border:0;padding:0;box-sizing:border-box;color:#6c6c6c;text-align:center}@media screen and (min-width: 600px) and (max-width: 900px){.header-bottom{background-position:55% 0% !important}}@media screen and (max-width: 600px){.header-bottom{background-image:none !important}}.homepage{display:flex;flex-direction:column}

.navigation{display:grid;grid-template-columns:1fr 4fr 1fr;align-items:center;background-color:#43c0f6;height:64px;width:100vw !important;text-transform:uppercase;font-weight:600;font-size:14px}.navigation *{color:white}.navigation .logo img{align-self:center}.navigation nav{height:100%;display:flex;flex-direction:column;justify-content:flex-end}.navigation nav ul{position:relative;display:flex;list-style:none;justify-content:center;align-items:center}.navigation nav ul a{margin:0 1rem 0.5rem 0;text-decoration:none}.navigation nav .login-language{height:100%;display:flex;flex-direction:column;align-items:flex-end}

footer{width:100vw;background-color:#b2e4fa;display:flex;justify-content:center;font-size:16px}footer .footer-wrapper{width:50%;margin:0 200px;display:grid;grid-template-columns:1fr 1fr 1fr 1fr 2fr}footer div{display:flex;flex-direction:column}a,a:hover,a:visited{text-decoration:none}

.slideshow{position:relative;width:100%}.slideshow img{width:100%}.slideshow .slide-controller{height:150px;align-items:center}.slideshow .slide-controller span.slide-dot{cursor:pointer;height:15px;width:15px;display:inline-block;border-radius:50%;background:#717171;margin:0 2px}

.icon-wrapper{position:relative;display:flex;flex-direction:row;flex-wrap:wrap}.icon-wrapper .icon{display:flex;flex-direction:column;align-items:center;justify-content:space-around;margin:1rem;border-radius:0.5rem;background-repeat:no-repeat;width:10rem;height:10rem}.icon-wrapper .icon img{width:5vmin;height:5vmin}

.modal-login{width:80vw;display:grid;grid-template-columns:1fr 1fr;margin:auto;position:absolute;top:30px;left:50%;transform:translateX(-50%);border:solid 3px black;background-color:white;z-index:999}.modal-login img{width:70%}.modal-login .login-signup-switch{width:100%;display:flex;flex-direction:row;justify-content:space-evenly}.modal-login .student-login,.modal-login .teacher-login{padding:50px 0px;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.modal-login .student-login input,.modal-login .teacher-login input{border:solid 2px #a29f9f;border-radius:5px;margin:20px 0}.modal-login .student-login{border-right:solid 5px black}

.nav-teacher-wrapper{position:relative;width:30vmin;height:100%;background-color:#43c0f6;display:grid;grid-template-rows:2fr 4fr 5fr 2fr;padding-top:40px}.nav-teacher-wrapper .profile{position:relative;height:90px}.nav-teacher-wrapper .profile img{position:relative;width:80px;height:80px}.nav-teacher-wrapper .nav-teacher-side{position:relative;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.nav-teacher-wrapper .nav-teacher-side ul{width:100%;position:relative;margin-top:40px;display:flex;flex-direction:column;list-style-type:none}.nav-teacher-wrapper .nav-teacher-side a{position:relative;display:flex;flex-direction:row;width:100%;margin:10px 0px}.nav-teacher-wrapper .nav-teacher-side a img{position:relative;width:20px;height:20px;margin:0 35px}.nav-teacher-wrapper .nav-teacher-side a span{position:relative;text-transform:uppercase;display:block;color:white;font-weight:900;font-size:18px}.nav-teacher-wrapper .right-caret-container{display:flex;justify-content:flex-end}.nav-teacher-wrapper .right-caret-container .right-caret{padding:20px;background-color:#b2e4fa;border-top-left-radius:20px;border-bottom-left-radius:20px;height:100px}.nav-teacher-wrapper .right-caret-container .right-caret .right-triangle{width:0;height:0;border-top:30px solid transparent;border-bottom:30px solid transparent;border-right:30px solid #f91c84}.nav-teacher-wrapper .logout-setting-wrapper{display:flex;flex-direction:column;justify-content:flex-end;padding-bottom:30px}.nav-teacher-wrapper .logout-setting-wrapper .logout-holder{width:100%;display:flex;flex-direction:row;justify-content:space-evenly;padding:0 30px}.nav-teacher-wrapper .logout-setting-wrapper a{display:flex;flex-direction:column;margin:10px 0}.nav-teacher-wrapper .logout-setting-wrapper a img{align-self:center}.nav-teacher-wrapper .logout-setting-wrapper a span{color:white}.minimize .nav-teacher-wrapper{grid-template-rows:2fr 6fr 2fr 3fr;width:10vmin}.minimize .nav-teacher-wrapper .profile{width:10vmin}.minimize .nav-teacher-wrapper .nav-teacher-side{width:10vmin;position:relative}.minimize .nav-teacher-wrapper .nav-teacher-side img{width:50px;height:50px}.minimize .nav-teacher-wrapper .nav-teacher-side span{display:none !important}.minimize .nav-teacher-wrapper .logout-setting-wrapper{display:flex;flex-direction:column;justify-content:flex-end;width:10vmin}.minimize .nav-teacher-wrapper .logout-setting-wrapper .logout-holder{width:100%;display:flex;flex-direction:column;justify-content:space-evenly;padding:0 30px}.minimize .nav-teacher-wrapper .logout-setting-wrapper .item p{display:none}.minimize .nav-teacher-wrapper .right-caret-container{display:flex;justify-content:flex-start}.minimize .nav-teacher-wrapper .right-caret-container .right-caret{padding:20px;background-color:#b2e4fa;border-top-left-radius:0px;border-bottom-left-radius:0px;border-top-right-radius:20px;border-bottom-right-radius:20px;height:100px}.minimize .nav-teacher-wrapper .right-caret-container .right-caret .right-triangle{width:0;height:0;border-top:30px solid transparent;border-bottom:30px solid transparent;border-right:none;border-left:30px solid #f91c84}

.nav-teacher-top{width:100vw;height:60px;background-color:white;z-index:3}.nav-teacher-top img{position:absolute;top:0px;left:0px;text-align:left}

.nav-teacher-bottom{display:flex;justify-content:center;align-items:center;width:100vw;height:60px;background-color:white;z-index:3}.nav-teacher-bottom img{position:absolute;top:0px;left:0px;text-align:left}

.teacher-project-library .library-content .library-content-control{height:100px;margin:0 30px;display:grid;grid-template-columns:2fr 1fr 1fr;align-content:center;align-items:center}.teacher-project-library .library-content .library-content-control .button-wrapper{display:flex;flex-direction:row;align-content:right;text-align:right}.teacher-project-library .library-content .library-content-control .button-wrapper img{height:30px;width:30px;margin:0 10px}.teacher-project-library .library-content .library-content-control .button-wrapper button{font-weight:800;font-size:16px;height:30px;background-color:#d8f1fc}.teacher-project-library .library-content .library-content-control h1{text-align:left}.teacher-project-library .library-content .library-submission-feed{margin-left:50px;height:80%;width:92%}

.submission-item{display:flex;flex-direction:row;margin:20px 0;align-items:center}.submission-item .project-wrapper{display:grid;grid-template-columns:1fr 5fr 1fr;grid-template-rows:1fr;align-items:center;margin:0 20px;padding:10px 40px;width:90%;border-radius:20px;background-color:white}.submission-item .project-wrapper img{width:80px;height:80px}.submission-item .project-wrapper .submission-message{display:flex;align-items:flex-start;flex-direction:column;justify-content:center}.submission-item .project-wrapper .submission-timestamp{display:flex;flex-direction:column;align-items:flex-end}.submission-item .teacher-project-image{flex-direction:column;justify-content:center;margin-left:50px;display:none}.submission-item .teacher-project-image button{cursor:pointer}.active{display:flex !important}

.student-profile-wrapper{width:100%;display:grid;grid-template-columns:repeat(5, 1fr)}.student-profile-wrapper .student-profile-item{padding:30px;margin:30px;background-color:white;display:flex;flex-direction:column;justify-content:space-evenly;align-content:center;border-radius:50px}.student-profile-wrapper .student-profile-item img{align-self:center}.student-profile-wrapper .student-profile-item span{font-size:20px;font-weight:700}

.layout_teacher_nav{display:grid;grid-template-rows:60px 1fr 60px;height:100vh;width:100vw}.layout_teacher_nav .middle-wrapper{display:flex;flex-direction:row;width:100%;height:100%}.layout_teacher_nav .middle-wrapper .teacher-content-wrapper{height:100%;width:100%;background-color:#b2e4fa;display:flex;justify-content:center;align-items:center;align-items:center}.layout_teacher_nav .middle-wrapper .teacher-content-wrapper .teacher-content{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#d8f1fc;border-radius:40px;width:90%;height:90%}

.student-objective-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:30px}.student-objective-wrapper .learning-block-wrapper{display:flex;flex-direction:row}.student-objective-wrapper .learning-block-wrapper .block-image{display:flex;flex-direction:row}.student-objective-wrapper .learning-block-wrapper .block-image .image-sub{display:flex;flex-direction:column}.student-objective-wrapper .learning-block-wrapper .block-image img{margin:0 10px;align-self:flex-start}

.instruction-wrapper{height:80%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.instruction-wrapper span{width:60%}

.flex-center{display:flex;justify-content:center;align-items:center;height:100%;width:100%}.flex-center .video-wrapper{height:90%;width:90%}.flex-center .video-wrapper iframe{width:100%;height:100%}

.iframe-container{display:flex;justify-content:center;align-items:center;height:100%;width:100%}.iframe-container .iframe-wrapper{height:90%;width:90%}.iframe-container .iframe-wrapper object{width:100%;height:100%}

.submit-project-wrapper{display:flex;flex-direction:row;justify-content:center;padding:50px}.submit-project-wrapper *{margin:20px}.submit-project-wrapper button{background-color:#f91c85;width:200px;height:100px;color:white;border-radius:20px}

.flex-center{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#eeeeee;padding:20px}.flex-center .button-container{width:100%;text-align:right}.flex-center .button-container button{background-color:#f91c85;padding:5px;color:white;border-radius:5px}.flex-center .profile-wrapper{display:flex;flex-direction:row}.flex-center .profile-wrapper .info-container{display:flex;flex-direction:column;align-self:flex-start;margin:30px;padding:30px;background-color:white;border-radius:30px}.flex-center .profile-wrapper .info-container img{align-self:center;height:100px;margin:10px}.flex-center .profile-wrapper .info-container button{margin:10px 0px;padding:5px;border-radius:5px;border:solid 2px #43c0f6}.flex-center .profile-wrapper .info-container .info-content{display:grid;grid-template-columns:1fr 1fr;justify-items:left;font-size:20px}

.library-content-wrapper{display:grid;grid-template-columns:1fr 4fr;height:900px;overflow:hidden;padding:20px 50px}.library-content-wrapper .content-control{margin:100px 20px auto 50px;display:flex;flex-direction:column}.library-content-wrapper .content-control .control-wrapper,.library-content-wrapper .content-control h3{display:flex}.feed-control{display:grid;grid-template-columns:1fr 1fr}.feed-control .control-show-limit{margin-right:100px;display:flex;justify-content:flex-end}.library-control-bottom{display:flex;flex-direction:row;justify-content:flex-end;padding-right:100px}.library-control-bottom button{text-transform:uppercase}

.library-item-wrapper{height:100%;width:100%;display:flex;flex-direction:row;flex-wrap:wrap;padding:40px}.library-item{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:20px}.library-item img{width:100px;height:100px}.library-item .level-activity>span{text-transform:uppercase}

