.modal-login {
  width: 80vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: auto;
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  border: solid 3px black;
  background-color: white;
  z-index: 999;
  img {
    width: 70%;
  }
  .login-signup-switch {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
  .student-login,
  .teacher-login {
    padding: 50px 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    input {
      border: solid 2px #a29f9f;
      border-radius: 5px;
      margin: 20px 0;
    }
  }
  .student-login {
    border-right: solid 5px black;
  }
}
