.navigation {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  align-items: center;
  background-color: #43c0f6;
  height: 64px;
  width: 100vw !important;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  * {
    color: white;
  }
  .logo {
    img {
      align-self: center;
    }
  }
  nav {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    ul {
      position: relative;
      display: flex;
      list-style: none;
      justify-content: center;
      align-items: center;
      a {
        margin: 0 1rem 0.5rem 0;
        text-decoration: none;
      }
    }
    .login-language {
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      // justify-content: flex-end;
    }
  }
}
