@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext");
// SCSS is the same as CSS, it will be automatically compile
//  They are more flexible: we can add variable to color scheme to maintain consitency
//  We can also nesting to provide cleaner code
