.slideshow {
  position: relative;
  width: 100%;
  img {
    width: 100%;
  }
  .slide-controller {
    height: 150px;
    align-items: center;
    span.slide-dot {
      cursor: pointer;
      height: 15px;
      width: 15px;
      display: inline-block;
      border-radius: 50%;
      background: #717171;
      margin: 0 2px;
    }
  }
}
